<template>
  <div id="app" class="form-container">
    <div class="form">

      <div class="form-group">
        <h2> FORMULÁRIO </h2>
      <label for="name">Nome completo:</label> 
      <input type="text" id="name" placeholder="Seu nome completo..." v-model="name" :class="{ 'is-invalid': !nameValid }"/> 
      </div>

      <div class="form-group">
        <label for="email">Endereço de e-mail:</label> 
        <input type="email" id="email" placeholder="Seu e-mail..." v-model="email" :class="{ 'is-invalid': !emailValid }"/> 
      </div>

      <div class="form-group">
        <label for="age">Idade:</label> <br>
        <input type="number" id="age" placeholder="Sua idade..." v-model="age" :class="{ 'is-invalid': !ageValid }"/> <br>
      </div>

      <div class="form-group">
        <label for="description">Descrição:</label> <br>
        <textarea id="description" placeholder="Fale um pouco sobre você..." v-model="description" :class="{ 'is-invalid': !descriptionValid }"></textarea> <br>
      </div>

      <div class="form-buttons">
        <button type="button" @click="validateAndSubmitForm" :disabled="formSubmitted" class="btn btn-primary"> {{ formSubmitted ? 'Enviado' : 'Enviar' }}
        </button>
        <button type="button" @click="clearForm" class="btn btn-secondary"> Limpar </button>
      </div>
    </div>

    <div class="submitted-data" v-if="formSubmitted">
      <h3> Dados do formulário </h3>
      <ul>
        <strong>Nome:</strong> {{ submittedData.name }} <br>
        <strong>E-mail:</strong> {{ submittedData.email }} <br>
        <strong>Idade:</strong> {{ submittedData.age }} <br>
        <strong>Descrição:</strong> {{ submittedData.description }} <br>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      name: "",
      email: "",
      age: null,
      description: "",
      nameValid: true,
      emailValid: true,
      ageValid: true,
      descriptionValid: true,
      formSubmitted: false,
      submittedData: {
        name: "",
        email: "",
        age: null,
        description: "",
      },
    };
  },
  methods: {
    validateAndSubmitForm() {
      this.nameValid = !!this.name;
      this.emailValid = /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(this.email); // Email válido
      this.ageValid = !isNaN(this.age) && this.age > 0; // Idade válida
      this.descriptionValid = !!this.description;

      if (this.nameValid && this.emailValid && this.ageValid && this.descriptionValid) {
        this.submitForm();
      } else {
        alert("Por favor, preencha todos os campos corretamente.");
      }
    },
    submitForm() {
      const userData = {
        name: this.name,
        email: this.email,
        age: this.age,
        description: this.description,
      };
      this.submittedData = userData;
      this.formSubmitted = true;
    },
    clearForm() {
      this.name = "";
      this.email = "";
      this.age = null;
      this.description = "";
      this.nameValid = true;
      this.emailValid = true;
      this.ageValid = true;
      this.descriptionValid = true;
      this.formSubmitted = false;
      this.submittedData = {
        name: "",
        email: "",
        age: null,
        description: "",
      };
    },
  },
};
</script>

<style scoped>
h2 {
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgb(115, 115, 232);
}

h3 {
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgb(115, 115, 232);
}
.form-container {
  display: grid;
  justify-content: center;
  align-items:center;
  height: 100vh;
  background-color: white;
  font-family: 'Times New Roman', Times, serif;
}

.form {
  max-width: 400px;
  width: 100%;
  padding: 40px;
  background-color: white;
  border-radius: 16px;
  box-shadow: 0 0 10px rgb(150, 150, 233);

}

.form-group {
  margin-bottom: 20px;
}

label {
  font-family:'Times New Roman', Times, serif;
  position: center;
  margin-bottom: 8px;
  color:black 30px;
  background-position:100%;
}

input {
  font-family: 'Times New Roman', Times, serif;
  width: 400px;
  height: 25px;
  position: relative;
  border-color: blue;
  border-radius: 4px;


}

textarea {
  resize: both; 
  overflow: auto;
  width: 400px; 
  min-height: 50px;
  min-width: 200px; 
  max-width: 100%; 
  font-family: 'Times New Roman', Times, serif;
  border-radius: 4px;
  border-color:blue;
}

.form-control {
  width: 100%;
  padding: 10px;
  font-size: 14px;
  border: 1px solid #ddd;
  border-radius: 4px;
  box-sizing: border-box;
  position: center;
}

button {
  width: 48%;
  padding: 12px;
  font-size: 16px;
  margin-right: 4%;
  margin-bottom: 10px;
  cursor: pointer;
  position: center;
  border-radius: 4px;
  background-color: rgb(174, 174, 241);
  border-block-color: blue;
  
}

.form-buttons {
  display: flex;
  justify-content: space-between;
}

strong{
  color:#8a71cd;
}
.submitted-data {
  margin-top: 10px;
  text-align: left;
  padding: 20px;
  border: 1px solid blue;
  border-radius: 8px;
  width: auto;
  box-shadow:#8a71cd;

}

.is-invalid {
  border: 1px solid red;
}
</style>

